@import "./index.scss";

.App {
  min-height: calc(100vh - #{$navbar-height});
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.App-content {
  width: 100%;
  padding: 20px;
  flex-direction: column;
  display: flex;
}

.Login-page {
  flex-direction: column;
  display: flex;
  justify-content: center;

  .error-container {
    margin-top: 15px;
  }
}

.Quiz-dashboard {
  display: flex;
  flex-direction: column;
  justify-items: flex-start;
  width: 100%;
}

@media screen and (min-width: 768px) {
  .App {
    /*height: 100vh;
    overflow: hidden;*/
  }

  .App-content, .Quiz-dashboard, .columns {
    flex: 1;
    overflow:hidden;
  }

  .Quiz-dashboard .question, .Quiz-dashboard .participants {
    max-height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-color: #7f7f7f white; /* thumb and track color */
    scrollbar-width: thin;
  }
}

.Quiz-dashboard .question, .Quiz-dashboard .participants {
  padding: 15px;
  margin: 10px;
}


::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:rgba(255, 255, 255, 0.1);border-radius:0px}::-webkit-scrollbar-thumb{background:rgba(255, 255, 255, 0.3);border-radius:0px}


.clickable {
  cursor: pointer;
}